@font-face {
  font-family: "Mplus";
  src: url("../fonts/mplus1p.woff2") format("woff2"), url("../fonts/mplus1p.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mplus-bold";
  src: url("../fonts/mplus1p-bold.woff2") format("woff2"), url("../fonts/mplus1p-bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
html {
  height: 100%;
}

body {
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Mplus", "Arial", sans-serif;
  color: #ffffff;
  font-size: 16px;
  line-height: 23px;
  font-weight: 400;
  background-color: #0B0A0D;
}

.main {
  flex: 1;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: calc(100% - 30px);
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 92%;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 1240px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
.header {
  position: relative;
  padding-bottom: 60px;
}

.header__slider {
  position: relative;
  background-color: #0B0A0D;
  padding-top: 150px;
}

.header__slide {
  background-color: #f5f1ee;
  margin-left: 15px;
  margin-right: 15px;
  padding-top: 207px;
  padding-left: 20px;
  height: 450px;
  box-sizing: border-box;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 6px;
}

.header__title {
  font-weight: 500;
  font-size: 40px;
  line-height: 85px;
  padding: 0;
  margin: 0;
}

.header__slide--first {
  background-image: url("../img/slide1-mobile.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header__slide--first {
    background-image: url("../img/slide1-mobile@2x.jpg");
    background-size: auto 450px;
  }
}

.header__slide--second {
  background-image: url("../img/slide1-mobile.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header__slide--second {
    background-image: url("../img/slide1-mobile@2x.jpg");
    background-size: auto 450px;
  }
}

.header__slide--third {
  background-image: url("../img/slide1-mobile.jpg");
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header__slide--third {
    background-image: url("../img/slide1-mobile@2x.jpg");
    background-size: auto 450px;
  }
}

.header__slider .slick-prev {
  left: 44px;
  bottom: -28px;
  top: auto;
  width: 100px;
  height: 0;
}
.header__slider .slick-prev::before {
  content: "";
  position: absolute;
  display: block;
  width: 100px;
  height: 26px;
  opacity: 1;
  background-repeat: no-repeat;
  background-image: url("../img/arrow-left.svg");
  z-index: 10;
}

.header__slider .slick-next {
  bottom: -28px;
  top: auto;
  right: 44px;
  width: 100px;
  height: 0;
}
.header__slider .slick-next::before {
  content: "";
  position: absolute;
  display: block;
  width: 100px;
  height: 26px;
  opacity: 1;
  background-repeat: no-repeat;
  background-image: url("../img/arrow-right.svg");
  z-index: 10;
}

@media (min-width: 768px) {
  .header {
    padding-bottom: 100px;
  }
  .header__slide {
    margin-left: 0;
    margin-right: 0;
    padding-left: 50px;
    padding-top: 206px;
    height: 500px;
  }
  .header__slider .slick-slide {
    padding-top: 300px;
  }
  .header__title {
    font-size: 60px;
    line-height: 65px;
  }
  .header__slider .slick-prev {
    left: 30px;
  }
  .header__slider .slick-next {
    right: 30px;
  }
  .header__slide--first {
    background-image: url("../img/slide1.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .header__slide--first {
    background-image: url("../img/slide1@2x.jpg");
    background-size: auto 650px;
  }
}
@media (min-width: 768px) {
  .header__slide--second {
    background-image: url("../img/slide1.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .header__slide--second {
    background-image: url("../img/slide1@2x.jpg");
    background-size: auto 650px;
  }
}
@media (min-width: 768px) {
  .header__slide--third {
    background-image: url("../img/slide1.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .header__slide--third {
    background-image: url("../img/slide1@2x.jpg");
    background-size: auto 650px;
  }
}
@media (min-width: 768px) {
  .header__slider .slick-prev {
    left: calc(50vw - 132px);
    bottom: -62px;
  }
}
@media (min-width: 768px) {
  .header__slider .slick-next {
    right: calc(50vw - 120px);
    bottom: -62px;
  }
}
@media (min-width: 1300px) {
  .header__slide {
    width: 1240px;
    height: 714px;
    margin-right: 20px;
    margin-left: 20px;
  }
  .header__title {
    font-size: 80px;
    line-height: 85px;
  }
  .header__slider .slick-slide {
