  top: 30px;
  right: 0;
  display: block;
  height: calc(100vh - 30px);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  overflow-y: auto;
  background-color: #0B0A0D;
}

@media (min-width: 768px) {
